import React, { Component } from 'react'
import glamorous from 'glamorous'
import titleize from 'titleize';

import { Wrapper } from './Grid'
import StepCounter from './StepCounter'

import sad_img from './sad.png'
import neu_img from './neu.png'
import hap_img from './hap.png'
import ang_img from './ang.png'


const ContentDiv = glamorous.div({
  paddingTop: '50px',
  paddingBottom: '50px',
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'column'
})

class StepFive extends Component {

  renderEmotion = (emotion) => {
    switch (emotion.emo){
      case 'ang':
        emotion.img=ang_img
        emotion.txt = "angry"
        break
      case 'hap':
        emotion.img=hap_img
        emotion.txt = "happy"
        break
      case 'neu':
        emotion.img=neu_img
        emotion.txt = "neutral"
        break
      case 'sad':
        emotion.img=sad_img
        emotion.txt = "sad"
        break}
    return (

      <div className="row">

        <div className="col-l-2">
        </div>
        <div className="col-l-8">
          <p>So it seems that you are <strong> {emotion.txt} </strong>, arent you? </p>
          <img src={emotion.img} alt="logo" width="120px" height="120px" style={{display: "block", margin : "auto"}}/>
          <table style={{margin: "auto"}}>
            <caption>Probabilities</caption>
            <thead>
              <tr>
                <th> Angry</th>
                <th> Happy</th>
                <th> Neutral</th>
                <th> Sad</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td> {(parseFloat(emotion.prob[0][0])).toFixed(3)} </td>
                <td> {(parseFloat(emotion.prob[0][1])).toFixed(3)} </td>
                <td> {(parseFloat(emotion.prob[0][2])).toFixed(3)} </td>
                <td> {(parseFloat(emotion.prob[0][3])).toFixed(3)} </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="col-l-2">
        </div>
      </div>
    )
  }

  render() {

    const emotion = this.props.getVerification()

    return (
      <Wrapper>
      
        <StepCounter steps={this.props.steps} currentStep={this.props.currentStep} />
        
        <ContentDiv>
          { this.renderEmotion(emotion)}
        </ContentDiv>

      </Wrapper>
    )
  }

}

export default StepFive



