import React, { Component } from 'react'
import { v1 } from 'uuid'

import Header from './Header'
import Footer from './Footer'
import StepOne from './StepOne'
import StepTwo from './StepTwo'
import StepThree from './StepThree'
import StepFour from './StepFour'
import StepFive from './StepFive'

class App extends Component {

  constructor(props) {
    super(props)
    
    this.state = {
      uuid: v1(),
      currentStep: 0,
      name: ''
    }

    this.steps = [
      { component: <StepOne nextStep={this.nextStep} steps="5" currentStep="1" /> },
      { component: <StepTwo nextStep={this.nextStep} steps="5" currentStep="2" uuid={this.state.uuid} setName={this.setName.bind(this)} /> },
      { component: <StepThree nextStep={this.nextStep} steps="5" currentStep="3" getName={this.getName.bind(this)} /> },
      { component: <StepFour nextStep={this.nextStep} steps="5" currentStep="4" uuid={this.state.uuid} getName={this.getName.bind(this)} setVerificationPhase={this.setVerificationPhase.bind(this)} /> },
      { component: <StepFive steps="5" currentStep="5" getName={this.getName.bind(this)} getVerification={this.getVerification.bind(this)} />}
    ]

    this.steps = [
      { component: <StepThree nextStep={this.nextStep} steps="3" currentStep="1" getName={this.getName.bind(this)} /> },
      { component: <StepFour nextStep={this.nextStep} steps="3" currentStep="2" uuid={this.state.uuid} getName={this.getName.bind(this)} setVerificationPhase={this.setVerificationPhase.bind(this)} /> },
      { component: <StepFive steps="3" currentStep="3" getName={this.getName.bind(this)} getVerification={this.getVerification.bind(this)} />}
    ]

  }

  getCurrentStep = () => this.steps[this.state.currentStep].component

  nextStep = () => {
    const next = this.state.currentStep + 1
    this.setState({ ...this.state, currentStep: next })
  }

  previousStep = () => {
    const next = this.state.currentStep - 1
    this.setState({ ...this.state, currentStep: next })
  }


  getName = () => this.state.name

  setName = (name) => {
    this.setState({ ...this.state, name })
  }

  getVerification = () => this.state.verification

  setVerificationPhase = (result) => {
    
    this.setState({ ...this.state, verification: result })
  }

  render() {
    return (
      <div className="main-wrapper">
        <Header/>
        <div className="page">
          {this.getCurrentStep()}
        </div>
        <Footer/>
      </div>
    );
  }
}

export default App;



