import React from 'react'
import glamorous from 'glamorous'

const Label = glamorous.div({
  color: '#666666'
})

const Wrapper = glamorous.div({
  height: '20px',
  width: '436px',
  backgroundColor: '#eef3f6',
  borderRadius: '20px'
})

const Progress = glamorous.div({
  height: '20px',
  backgroundColor: '#1781e3',
  borderTopLeftRadius: '20px',
  borderBottomLeftRadius: '20px'
}, ({ steps, currentStep }) => ({
  width: (436/parseInt(steps, 10)) * (parseInt(currentStep, 10)),
  borderTopRightRadius: (436/parseInt(steps, 10)) * (parseInt(currentStep, 10)) > 430 ? '20px' : '0',
  borderBottomRightRadius: (436/parseInt(steps, 10)) * (parseInt(currentStep, 10)) > 430 ? '20px' : '0'
}))

const StepCounter = ({ steps, currentStep }) => {
  return (
    <div>
      <Label>{currentStep} of {steps}</Label>
      <Wrapper>
        <Progress steps={steps} currentStep={currentStep} />
      </Wrapper>
    </div>
  )
}

export default StepCounter



