import React from 'react'

const Header = () => {
  return (
    <div className="nav" data-init="auto">
      <nav className="nav__level1">
        <div className="container">
          <a className="logo" href="/">
            <span className="logo-lifeform" aria-label="Swisscom Logo"></span>
            <span className="logo-type">
              <img src="speech_emotion/images/svgs/swisscom-logo-type.svg" alt="Swisscom" />
            </span>
          </a>
          <div className="row">
          </div>
        </div>
      </nav>
    </div>
  )
}

export default Header


