import React, { Component } from 'react'
import glamorous from 'glamorous'
import titleize from 'titleize';

import { Button } from 'sdx-react'

import { Wrapper } from './Grid'
import StepCounter from './StepCounter'

const ContentDiv = glamorous.div({
  paddingTop: '50px',
  paddingBottom: '50px',
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'column'
})

class StepThree extends Component {

  render() {
    return (
      <Wrapper>
      
        <StepCounter steps={this.props.steps} currentStep={this.props.currentStep} />
        
        <ContentDiv>
            <p>Hello, welcome to the Speech Emotion Recognition platform</p>
            <p>To proceed to emotion evaluation, click on Next.</p>
        </ContentDiv>

        <glamorous.Div>
          <Button onClick={this.props.nextStep} label="Next" icon="004-arrow-right" primary />
        </glamorous.Div>

      </Wrapper>
    )
  }

}

export default StepThree



