import React, { Component } from 'react'
import glamorous from 'glamorous'

import { Wrapper } from './Grid'
import StepCounter from './StepCounter'
import RecordButton from './RecordButton'

import loading from './loading2.gif'

const ContentDiv = glamorous.div({
  paddingTop: '50px',
  paddingBottom: '50px',
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'column'
})

class StepFour extends Component {

  constructor(props) {
    super(props)
    this.state = {
      isLoading: false,
      failed: false
    }
  }

  verify({ blob, buffer}) {

    const formData = new FormData()
    formData.append('file', blob)
    formData.append('uuid', this.props.uuid)
    formData.append('username', this.props.getName())

    this.setState({ isLoading: true, failed: false })
    console.log("Changing state")
    console.log(this.state.isLoading)
    
    fetch('http://49731ef0.ngrok.io/identify', {
      method: 'POST',
      body: formData
    }).then((response) => {
      console.log(response)
      return response.text()
    }).then((body) => {
      console.log(JSON.parse(body))
      this.props.setVerificationPhase(JSON.parse(body))
      this.props.nextStep()
    }).catch((e) => {
      console.log(e)
      this.setState({ ...this.state, failed: true })
    })

  }

  failState() {
    
        const FailWrapper = glamorous.div({
          margin: '50px',
          color: 'tomato',
          fontFamily: 'bold'
        })
    
        return (
          <FailWrapper>
            <p>Hoppla da hat etwas nicht geklappt.</p>
          </FailWrapper>
        )
      }

  render() {
    if (this.state.isLoading) {
      return (
        <Wrapper>
        
          <StepCounter steps={this.props.steps} currentStep={this.props.currentStep} />
          
          <ContentDiv>
              <p>Wait... Your voice is being processed</p>
          </ContentDiv>

          <div>
            <img src={loading} alt="logo" width="146px" height="124px" />
          </div>

          { this.state.failed && this.failState() }

        </Wrapper>
      )
    }
    else {
      return (
        <Wrapper>
        
          <StepCounter steps={this.props.steps} currentStep={this.props.currentStep} />
          
          <ContentDiv>
              <p>Please click on the record button and speak for a few seconds in order for the system to record you</p>
          </ContentDiv>

          <glamorous.Div>
            <RecordButton getData={this.verify.bind(this)} />
          </glamorous.Div>

          { this.state.failed && this.failState() }

        </Wrapper>
      )
    }
  }

}

export default StepFour



