import React, { Component } from 'react'
import glamorous from 'glamorous'

import { Button } from 'sdx-react'

import { Wrapper } from './Grid'
import StepCounter from './StepCounter'

const ContentDiv = glamorous.div({
  paddingTop: '50px',
  paddingBottom: '50px',
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'column'
})

class StepOne extends Component {
  render() {
    return (
      <Wrapper>

        <StepCounter steps={this.props.steps} currentStep={this.props.currentStep} />

        <ContentDiv>
          <p>Guten Tag und willkommen zur Anrufer-Authentisierungs-Demo.</p>
          <p>Bitte drcken Sie Start um zu beginnen.</p>
        </ContentDiv>

        <glamorous.Div>
          <Button onClick={this.props.nextStep} label="Start" icon="004-arrow-right" primary />
        </glamorous.Div>

      </Wrapper>
    )
  }
}

export default StepOne



