import React, { Component } from 'react'
import glamorous from 'glamorous'

import { Wrapper } from './Grid'
import StepCounter from './StepCounter'
import RecordButton from './RecordButton'

const ContentDiv = glamorous.div({
  paddingTop: '50px',
  paddingBottom: '50px',
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'column'
})

class StepTwo extends Component {

  constructor(props) {
    super(props)
    this.state = {
      isLoading: false,
      failed: false
    }
  }

  identify({ blob, buffer }) {

    this.setState({ ...this.state, buffer, isLoading: true })
    
    const formData = new FormData()
    formData.append('uuid', this.props.uuid)
    formData.append('file', blob)
    
    fetch('http://52.19.79.25:8080/identify', {
      method: 'POST',
      body: formData
    }).then((response) => {
      console.log(response)
      return response.text()
    }).then((body) => {
      console.log(body)
      this.props.setName(body)
      this.props.nextStep()
    }).catch(e => {
      console.log(e)
      this.setState({ ...this.state, failed: true })
    })

  }

  failState() {

    const FailWrapper = glamorous.div({
      margin: '50px',
      color: 'tomato',
      fontFamily: 'bold'
    })

    return (
      <FailWrapper>
        <p>Please pardon our dust... something hasn't worked out.</p>
      </FailWrapper>
    )
  }

  render() {
    return (
      <Wrapper>

        <StepCounter steps={this.props.steps} currentStep={this.props.currentStep} />
        
        <ContentDiv>
            <p>Wie heissen Sie?</p>
            <p>Zum Sprechen, klicken Sie bitte auf das Mikrofon.</p>
        </ContentDiv>

        <glamorous.Div display="flex">
          <RecordButton getData={this.identify.bind(this)} />
        </glamorous.Div>

        { this.state.failed && this.failState() }

      </Wrapper>

    )
  }
}

export default StepTwo



