import React, { Component } from 'react'
import glamorous from 'glamorous'
import Recorder from 'recorder-js'

import mic from './mic@2x.png'

const MicButton = glamorous.div({
  borderRadius: '50%',
  backgroundColor: '#1781e3',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  width: '80px',
  height: '80px'
})

const RecordingButton = glamorous.div({
  borderRadius: '50%',
  backgroundColor: '#be0000',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  width: '80px',
  height: '80px'
})

class RecordButton extends Component {

  constructor(props) {
    super(props)

    this.audioContext = new (window.AudioContext || window.webkitAudioContext)()
    this.recorder = new Recorder(this.audioContext)

    navigator.mediaDevices
      .getUserMedia({ audio: true })
      .then((stream) => this.recorder.init(stream))
      .catch((e) => console.log(e))

    this.state = {
      isRecording: false
    }
  }

  onClick = (evt) => {
    this.setState((prevState) => ({ isRecording: !prevState.isRecording }))
    if (this.state.isRecording) {
      this.recorder.stop().then((result) => {
        this.props.getData(result)
      })
    } else {
      this.recorder.start()
    }
  }

  renderMic() {
    return (
      <MicButton>
        <img src={mic} alt="logo" width="20px" height="34px" />
      </MicButton>
    )
  }

  renderRecording() {
    return (
      <RecordingButton>
        <glamorous.Div width="22.4px" height="22.4px" borderRadius="4px" backgroundColor="#ffffff"></glamorous.Div>
      </RecordingButton>
    )
  }

  render() {
    return (
      <div onClick={this.onClick}>
        { this.state.isRecording ? this.renderRecording() : this.renderMic() }
      </div>
    )
  }

}

export default RecordButton



